var tf=require('@tensorflow/tfjs');export function teach(randomPointsArray,nu,weights01Array,weights02Array,weights2Array,teachOutputArray,count){tf.setBackend('cpu');var randomPoints=tf.tensor(randomPointsArray);var weights01=tf.tensor(weights01Array);var weights02=tf.tensor(weights02Array);var weights2=tf.tensor(weights2Array);var teachOutput=tf.tensor(teachOutputArray);var randomSliceIndex=function randomSliceIndex(tensor){return 0+Math.random()*(tensor.shape[0]-1-0)|0;};var input=tf.concat([randomPoints,tf.onesLike(teachOutput)],1);var e6;var y6;for(var i=0;i<count;i++){//Прямой прогон
//Считаем выходы слоев
console.log(i);var randomIndex=randomSliceIndex(input);var slicedInput=input.slice(randomIndex,1);var slicedOutput=teachOutput.slice(randomIndex,1);var net4=tf.dot(slicedInput,weights01);var net5=tf.dot(slicedInput,weights02);var y4=net4.sigmoid();var y5=net5.sigmoid();var hiddenOutput=tf.concat([tf.onesLike(y4),y4,y5],1);var net6=tf.dot(hiddenOutput,weights2);y6=net6.sigmoid();//Считаем ошибки
e6=slicedOutput.sub(y6).mul(y6).mul(tf.onesLike(y6).sub(y6));var e5=y5.mul(tf.onesLike(y5).sub(y5)).mul(e6).mul(weights2.slice(2,1));var e4=y4.mul(tf.onesLike(y4).sub(y4)).mul(e6).mul(weights2.slice(1,1));// Считаем дельты весов
var dW65=y5.mul(e6).mul(tf.scalar(nu));var dW64=y4.mul(e6).mul(tf.scalar(nu));var dW63=tf.onesLike(y4).mul(e6).mul(tf.scalar(nu));var dW2=tf.concat([dW65,dW64,dW63],1);var dW52=e5.mul(slicedInput.transpose().slice(0,1)).mul(tf.scalar(nu));var dW51=e5.mul(slicedInput.transpose().slice(1,1)).mul(tf.scalar(nu));var dW50=e5.mul(tf.scalar(1)).mul(tf.scalar(nu));var dW02=tf.concat([dW52,dW51,dW50],1);var dW42=e4.mul(slicedInput.transpose().slice(0,1)).mul(tf.scalar(nu));var dW41=e4.mul(slicedInput.transpose().slice(1,1)).mul(tf.scalar(nu));var dW40=e4.mul(tf.scalar(1)).mul(tf.scalar(nu));var dW01=tf.concat([dW42,dW41,dW40],1);// Корректировка весов
weights2=weights2.add(dW2.transpose());weights02=weights02.add(dW02.transpose());weights01=weights01.add(dW01.transpose());}console.log('Веса после тренировки:');console.log('входной слой');weights01.print();console.log('скрытый слой');weights02.print();console.log('выходной слой');weights2.print();console.log('Ошибка:');e6.abs().mean().print();return{weights01:weights01.arraySync(),weights02:weights02.arraySync(),weights2:weights2.arraySync()};}
addEventListener('message', function (e) {var ref = e.data;var type = ref.type;var method = ref.method;var id = ref.id;var params = ref.params;var f,p;if (type === 'RPC' && method) {if (f = __webpack_exports__[method]) {p = Promise.resolve().then(function () { return f.apply(__webpack_exports__, params); });} else {p = Promise.reject('No such method');}p.then(function (result) {postMessage({type: 'RPC',id: id,result: result});}).catch(function (e) {var error = {message: e};if (e.stack) {error.message = e.message;error.stack = e.stack;error.name = e.name;}postMessage({type: 'RPC',id: id,error: error});});}});postMessage({type: 'RPC',method: 'ready'});